  right: 30px;
  opacity: 0;
  visibility: hidden;
}

.header .owl-Carousel .owl-nav .owl-next {
  position: absolute;
  left: 30px;
  opacity: 0;
  visibility: hidden;
}

.header .owl-Carousel .owl-dots {
